import React from 'react';
import { ItemsManager } from '../ItemsManager';

import { Container } from '@material-ui/core';
import { PageTitle } from '../../shared/page-title/PageTitle';

import { IItem } from '../IItem';

import './Diy.css';

export class Diy extends ItemsManager {
  constructor(props) {
    super(props);
    this.state = {
      data: {
        allHousewareItems: [],
        allWallmountedItems: [],
        allMiscItems: []
      }
    }
  }

  componentDidMount() {
    this._getAllHousewareItems().then((housewares: any) => {
      console.log(housewares);
      this.renderItems(housewares);
    });
  }

  /** 
   * map data needed for the view from the response
   */
  renderItems(items: any) {
    const _allHouseware: IItem[] = [];

    items.forEach((itemGroup: any[]) => {
      itemGroup.forEach((item: any) => {
        if (item['isDIY']) {
          const _newdiyItem:IItem = {
            buyPrice: item['buy-price'],
            canCustomize: item['canCustomizeBody'] || item['canCustomizePattern'],
            color1: item['color-1'],
            color2: item['color-2'],
            hhaConcept1: item['hha-concept-1'],
            hh1Concept2: item['hha-concept-2'],
            imageUri: item['image_uri'],
            isCatalog: item['isCatalog'],
            isDiy: item['isDIY'],
            isInteractive: item['isInteractive'],
            isOutdoor: item['isOutdoor'],
            name: item['name']['name-USen'],
            pattern: item['pattern'],
            sellPrice: item['sell-price'],
            source: item['source'],
            tag: item['tag'],
            variant: item['variant']
          }

          _allHouseware.push(_newdiyItem);
        }
      });
    });

    console.log(_allHouseware);
  }

  render() {
    return (
      <div className="background-main padded-6y diy-wrapper">
        <Container>
          <div className="app-title-container">
            <PageTitle pageTitle="DIY Items" backLink="/" backLinkDisplay="Home" />
            <div className="app-search-wrapper">
              {/* <div className="app-search-icon"><SearchIcon fontSize="large" /></div>
              <TextField id="outlined-basic" label="Search Villager" variant="outlined" onChange={event => this.textSearchChange(event.target.value)} />  */}
            </div>
          </div>
          
          <div className="padded-2y padded-4x diy-container main-section">
            asdfj;laksdf
          </div>
        </Container>
      </div>
    )
  }
}